.DivPartnerBrands {
  width: 13rem;
  height: 13rem;
  background-color: var(--background_light);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 1rem;
  cursor: pointer;
  border: 1px solid var(--borderColor);
  box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.2);
  transition: 0.4s;
  margin-right: 2rem;
}

.DivPartnerBrands:hover {
  transform: scale(1.1);
}

.LogoPartnerBrands {
  width: 7rem;
  height: auto;
}
